import React, {useEffect, useState, useRef} from "react";
import echarts from 'echarts'
import ChartConfig from "../config/chart-config";
import moment from "moment";

const Chart = (props) => {
  const [chartOption, setChartOption] = useState({});
  const defaultConfig = ChartConfig();
  const chart = useRef(null);

  let chartInstance = null;
  function addZero(num) {
    let target = num.toString();
    if (target.length < 2) {
      target = `0${target}`;
    }
    return target;
  }
  function renderChart(chartData) {
    const renderInstance = echarts.getInstanceByDom(chart.current);
    const xAxis = [...chartData.xAxis];
    const yAxis = [...chartData.yAxis];
    const lastPoint = [xAxis[xAxis.length - 1], yAxis[yAxis.length - 1]];

    if (renderInstance) {
      chartInstance = renderInstance;
    } else {
      chartInstance = echarts.init(chart.current);
    }
    const last = xAxis[xAxis.length - 1];
    const lastValue = yAxis[yAxis.length - 1];
    const currentDate = `${moment().format('YYYY-MM-DD')} ${last}`;
    const currentSec = last ? last.split(':')[2] : '';
    const config = defaultConfig;

    for (let i = 1; i < 35; i++) {
      const date = moment(currentDate).add(i, 'second').format('HH:mm:ss');
      const sec = date.split(':')[2];
      xAxis.push(date);
      yAxis.push(null);
      // 배팅 마감 10, 25, 40, 55
      // 결과 15, 30, 45, 00

      if (parseInt(sec) % 15 === 10 || parseInt(sec) % 15 === 0) {
        let remainSec = parseInt(sec) - parseInt(currentSec);
        if (remainSec < 0) {
          remainSec += 60;
        }
        if (parseInt(sec) % 15 === 10) {
          config.series[0].markLine.data.push({ xAxis: date, name: `00:${addZero(remainSec)}`, lineStyle: { type: 'dotted' }});
        } else if (parseInt(sec) % 15 === 0) {
          config.series[0].markLine.data.push({ xAxis: date, name: `00:${addZero(remainSec)}`, lineStyle: { color: 'red' }});
        }
      }
    }

    if (lastValue) {
      config.series[0].markLine.data.push({
        yAxis: lastValue,
        name: lastValue,
        label: {
          formatter: '{b}',
          distance: [330, 10],
          position: 'insideEndTop'
        }
      });
    }
    // config.series[0].markLine.data[0] = { xAxis: last }

    config.xAxis.data = xAxis;
    config.series[0].data = yAxis;
    config.series[0].markPoint.data[0].coord = lastPoint;

    console.log('LAST DATA');
    console.log(last);
    console.log()
    chartInstance.setOption(config, {
      noMerge: true,
      lazyLoad: true
    });
  }
  useEffect(() => {
    renderChart(props.chartData);
  }, [props.chartData]);
  useEffect(() => {
    return () => {
      chartInstance && chartInstance.dispose();
    };
  }, []);
  return (
    <>
      <div
        ref={chart}
        style={{
          height: "800px",
          // background: "gray"
        }}
      />
    </>
  );
};

export default Chart;