import React, {useEffect, useState} from 'react';
import './App.css';
import moment from 'moment';
import Chart from "./components/chart";

function App() {

  const [chartData, setChartData] = useState({
    xAxis: [],
    yAxis: []
  });
  const [loopFlag, setLoopFlag] = useState(false);

  function randomIntFromInterval(min, max) { // min and max included
    return Math.floor(Math.random() * (max - min + 1) + min);
  }

  const genData = (unixTime) => {
    const local = moment(unixTime).local().format('HH:mm:ss');
    let baseLine = randomIntFromInterval(180123, 190123);
    baseLine = (baseLine / 100000).toFixed(5)
    // 1.80123
    // 1.90123
    // 180123
    // 190123

    return {
      local: local,
      data: baseLine
    }
  };

  useEffect(() => {
    const startTimestamp = moment.utc().valueOf() - 86400 * 1000;
    const endTimestamp = moment.utc().valueOf();
    const genChartData = {
      xAxis: [],
      yAxis: []
    };
    for (let i = startTimestamp; i < endTimestamp; i += 1000 * 60) {
      const genedData = genData(i);
      genChartData.xAxis.push(genedData.local);
      genChartData.yAxis.push(genedData.data);
    }
    setChartData(genChartData);
  }, []);

  React.useEffect(() => {
    const updateData = setTimeout(() => {
      const tmpChartData = {...chartData};
      const currentTime = moment.utc().valueOf();
      const genedData = genData(currentTime);

      tmpChartData.xAxis.shift();
      tmpChartData.xAxis.push(genedData.local);
      tmpChartData.yAxis.shift();
      tmpChartData.yAxis.push(genedData.data);
      console.log('----------');
      console.log(genedData.local);
      console.log(genedData.data);
      setChartData(tmpChartData);
    }, 1000);
    return () => {
      clearTimeout(updateData);
    };
  }, [chartData]);


  return (
    <div style={{width: '100vw', height: '100vh', backgroundColor: '#161821'}}>
      <Chart chartData={chartData} />
    </div>
  );
}

export default App;
